___CSS_LOADER_EXPORT___.push([module.id, `input:focus, textarea:focus{
    outline: none;
}

#project-dialog[open]{
    animation: dialogShow .5s forwards
}

#project-dialog{
    font-family: "Lato", "Roboto mono";
    border: none;
    overflow: visible;
    background-color: rgba(0, 0, 0,0);
    opacity: 1;
    padding:0;
    animation: dialogClose .5s ;
}

#project-dialog[open]::backdrop{
    animation: backdropOpen .5s forwards;
    
}

#project-form{
    display: flex;
    flex-direction: column;
    background-color: var(--PrimaryOne);
    gap:20px;
    padding:20px 25px 30px;
    border:none;
    border-radius:5px;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .45 );
}
#project-form > h2{
    color: var(--FontOne);
    margin-bottom:10px;
} 


#project-name{
    width: 500px;;
    font-size: 1.1rem;
    padding:10px 25px;
    border-radius:5px;
    border:none;
}

#project-name, #project-form > textarea{
    background-color: var(--TerTierOne);
    transition: .25s;
    color: var(--FontOne);
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .15 );
}

#project-name::placeholder, #project-form > textarea::placeholder{
    color: rgb(215, 215, 215);
}

#project-name:focus, #project-form > textarea:focus, #date-input:focus, #select-dif:focus{
    transform: scale(1.01);
    background-color: black;
    color: var(--TerTierOne);
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .25 );
}
#project-form > textarea{
    resize: none;
    border:none;
    border-radius:5px;
    padding:10px 15px;
    transition: .25s;
}

#date-input, #select-dif{
    color: var(--FontOne);
    background-color: var(--TerTierOne);
    padding:10px 25px;
    font-size: 1.1rem;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .15 );
    transition: .25s;
    cursor: pointer;
}
#date-input::-webkit-calendar-picker-indicator {
    filter: invert(1);
}

#select-icon{
    display: grid;
    gap:10px;
    grid-template-columns: repeat(6, minmax(50px, 1fr));
    grid-template-rows: 30px;
    grid-auto-rows: 80px;

}
#select-icon > h3{
    grid-column: 1/7;
    color: var(--FontOne);
}

img.project-icon{
    border: 2px solid black;
    background-color: var(--TerTierOne);
    border-radius: 3px;
    transition: .25s;
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .25 );
}   
.project-icon:hover{
    transform: scale(1.1);
}
img.project-icon.not-selected{
    filter: opacity(.5) blur(1px);
    transform: scale(.95);
}
img.project-icon.selected{
    transform: scale(1.15);
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .35 );
}



#project-confirm{
    align-self: end;
    font-weight: bold;
    padding:15px 25px;
    background-color: var(--TerTierOne);
    color: var(--FontOne);
    border:none;
    border-radius:5px;
    cursor:pointer;
    transition:.25s;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .25 );
}


/* Dialog animation */
@keyframes dialogShow {
    0%{
        display: none;
        opacity: 0;
        transform:translateX(-100%) ;
    }
    100%{
        display: block;
        opacity: 1;
        transform: translateX(0%);
    }
}

@keyframes dialogClose {
    0%{
        display: block;
        opacity: 1;
        transform: translateX(0%);
    }
    100%{
        display: none;
        opacity: 0;
        transform: translateX(100%);
    }
}

@keyframes backdropOpen {
    0%{
        background-color: rgba(0, 0, 0,0);
    }
    100%{
        background-color: rgba(0, 0, 0, .75 );
    }
}`, "",{"version":3,"sources":["webpack://./src/css/dialog.css"],"names":[],"mappings":"AAAA;IACI,aAAa;AACjB;;AAEA;IACI;AACJ;;AAEA;IACI,kCAAkC;IAClC,YAAY;IACZ,iBAAiB;IACjB,iCAAiC;IACjC,UAAU;IACV,SAAS;IACT,2BAA2B;AAC/B;;AAEA;IACI,oCAAoC;;AAExC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mCAAmC;IACnC,QAAQ;IACR,sBAAsB;IACtB,WAAW;IACX,iBAAiB;IACjB,qDAAqD;AACzD;AACA;IACI,qBAAqB;IACrB,kBAAkB;AACtB;;;AAGA;IACI,YAAY;IACZ,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;IACjB,WAAW;AACf;;AAEA;IACI,mCAAmC;IACnC,gBAAgB;IAChB,qBAAqB;IACrB,qDAAqD;AACzD;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,sBAAsB;IACtB,uBAAuB;IACvB,wBAAwB;IACxB,qDAAqD;AACzD;AACA;IACI,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;IACrB,mCAAmC;IACnC,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,qDAAqD;IACrD,gBAAgB;IAChB,eAAe;AACnB;AACA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,mDAAmD;IACnD,wBAAwB;IACxB,oBAAoB;;AAExB;AACA;IACI,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,uBAAuB;IACvB,mCAAmC;IACnC,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,qDAAqD;AACzD;AACA;IACI,qBAAqB;AACzB;AACA;IACI,6BAA6B;IAC7B,qBAAqB;AACzB;AACA;IACI,sBAAsB;IACtB,qDAAqD;AACzD;;;;AAIA;IACI,eAAe;IACf,iBAAiB;IACjB,iBAAiB;IACjB,mCAAmC;IACnC,qBAAqB;IACrB,WAAW;IACX,iBAAiB;IACjB,cAAc;IACd,eAAe;IACf,qDAAqD;AACzD;;;AAGA,qBAAqB;AACrB;IACI;QACI,aAAa;QACb,UAAU;QACV,4BAA4B;IAChC;IACA;QACI,cAAc;QACd,UAAU;QACV,yBAAyB;IAC7B;AACJ;;AAEA;IACI;QACI,cAAc;QACd,UAAU;QACV,yBAAyB;IAC7B;IACA;QACI,aAAa;QACb,UAAU;QACV,2BAA2B;IAC/B;AACJ;;AAEA;IACI;QACI,iCAAiC;IACrC;IACA;QACI,qCAAqC;IACzC;AACJ","sourcesContent":["input:focus, textarea:focus{\r\n    outline: none;\r\n}\r\n\r\n#project-dialog[open]{\r\n    animation: dialogShow .5s forwards\r\n}\r\n\r\n#project-dialog{\r\n    font-family: \"Lato\", \"Roboto mono\";\r\n    border: none;\r\n    overflow: visible;\r\n    background-color: rgba(0, 0, 0,0);\r\n    opacity: 1;\r\n    padding:0;\r\n    animation: dialogClose .5s ;\r\n}\r\n\r\n#project-dialog[open]::backdrop{\r\n    animation: backdropOpen .5s forwards;\r\n   