___CSS_LOADER_EXPORT___.push([module.id, `#main-container{
    display: flex;
    position: relative;
    margin: 50px 150px 50px 350px;
    transition: .25s;
}

#main-container.go-left{
    transform: translateX(-10%);
    margin-right:100px;
}

#main-content{
    display: flex;
    flex-direction: column;
    gap:30px;
    border: none;
    border-radius:10px;
    max-height: 600px;
    width: 100%;
    background-color: var(--PrimaryOne);
    color: var(--FontOne);
    padding:10px 30px 100px;
    position: relative;
    transition: .25s;
}

#top-main{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#top-main > h2{
    font-size: 1.5rem;
}
#top-main > img{
    height: 50px;;
    width: 150px;
    border: none;
    border-radius: 3px;;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .4);
}

#center-main{
    display: flex;
    flex-direction: column;
    gap:25px;
    flex:1;
    padding:10px 25px 0 15px ;
}
#center-main.scroll{
    overflow: hidden scroll;
}

div.tasks{
    display: flex;
    justify-content: space-between ;
    align-items: center;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .2);
    min-height: 35px;;
    padding:10px 30px;
    background-color: var(--TerTierOne);
    transition: .25s;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

div.tasks:hover{
    transform: scale(1.01);
}

div.tasks.erase{
    animation: taskDelete .4s forwards ;
    pointer-events: none;
}

#dif-meter{
    position: absolute;
    left: -98%;
    top:0;
    bottom: 0;
    border:none ;
    border-radius: 40px;
    width: 100%;
    transition: .35s;
}

#dif-meter.easy{
    background-color: lightgreen;
}
#dif-meter.medium{
    background-color: orange;
}
#dif-meter.hard{
    background-color: red;
}

#dif-meter.complete{
    transform: translateX(196.5%);
}

div.tasks.complete{
    filter: opacity(.7) blur(.5px);
}

#button-container{
    display: flex;
    flex-direction: row;
    align-items: center; 
    gap:10px;
}

#button-container > p{
    margin-right: 30px;
}

#button-container > p.past-day{
    color: black;
    text-shadow: 0px 0px 5px rgba(255, 0, 0, .7);
}

#button-container > p.deadline{
    color: darkred;
    text-shadow: 1px 2px 5px rgba(0, 0, 0, .4);
}

#button-container > p.one-week{
    color: orange;
    text-shadow: 1px 2px 5px rgba(255, 255, 255, .4);
}

#button-container > p.more-than-a-week{
    color: lightgreen;
    text-shadow: rgba(255, 255, 255, .4);
}

#button-container > img{
    height: 30px;
    width: 30px;
    padding: 3px;
    border: none;
    border-radius: 50%;
    transition: .25s;
    background-color: rgb(218, 216, 216);
}

#button-container > img:hover{
    background-color: black;
}

#new-task-button{
    background-color: var(--TerTierOne);
    color: var(--FontOne);
    font-weight: bold;
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .2);
    position: absolute;
    bottom:20px;
    right:30px;

    padding:10px 20px;
    border: none;
    border-radius: 5px;;
    transition: .25s;
    cursor: pointer;
}

#new-task-button:hover{
    transform: scale(1.05);
    background-color: black;
}


/* Animation */
@keyframes taskDelete {
    0%{
        opacity: 1;
        transform: scale(1);
    }
    100%{
        opacity: 0;
        transform: scale(150%) translateY(-50%);
    }
}`, "",{"version":3,"sources":["webpack://./src/css/main.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,kBAAkB;IAClB,6BAA6B;IAC7B,gBAAgB;AACpB;;AAEA;IACI,2BAA2B;IAC3B,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,WAAW;IACX,mCAAmC;IACnC,qBAAqB;IACrB,uBAAuB;IACvB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;AACA;IACI,iBAAiB;AACrB;AACA;IACI,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,mDAAmD;AACvD;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,MAAM;IACN,yBAAyB;AAC7B;AACA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,mBAAmB;IACnB,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,mDAAmD;IACnD,gBAAgB;IAChB,iBAAiB;IACjB,mCAAmC;IACnC,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,mCAAmC;IACnC,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,KAAK;IACL,SAAS;IACT,YAAY;IACZ,mBAAmB;IACnB,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,4BAA4B;AAChC;AACA;IACI,wBAAwB;AAC5B;AACA;IACI,qBAAqB;AACzB;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,4CAA4C;AAChD;;AAEA;IACI,cAAc;IACd,0CAA0C;AAC9C;;AAEA;IACI,aAAa;IACb,gDAAgD;AACpD;;AAEA;IACI,iBAAiB;IACjB,oCAAoC;AACxC;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,oCAAoC;AACxC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,mCAAmC;IACnC,qBAAqB;IACrB,iBAAiB;IACjB,mDAAmD;IACnD,kBAAkB;IAClB,WAAW;IACX,UAAU;;IAEV,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,sBAAsB;IACtB,uBAAuB;AAC3B;;;AAGA,cAAc;AACd;IACI;QACI,UAAU;QACV,mBAAmB;IACvB;IACA;QACI,UAAU;QACV,uCAAuC;IAC3C;AACJ","sourcesContent":["#main-container{\r\n    display: flex;\r\n    position: relative;\r\n    margin: 50px 150px 50px 350px;\r\n    transition: .25s;\r\n}\r\n\r\n#main-container.go-left{\r\n    transform: translateX(-10%);\r\n    margin-right:100px;\r\n}\r\n\r\n#main-content{\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap:30px;\r\n    borde